#pokemon-screenBox {
  width: 100%;
  height: 100%;
  min-height: 540px;
  position: relative;
  background-color: #000; }
  #pokemon-screenBox #pokemon-loading {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
    background: black;
    color: white; }
  #pokemon-screenBox .pokemon-screen {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 400px !important;
    height: 300px !important; }

div.max_modal #pokemon-screenBox {
  width: 100% !important;
  height: 90% !important; }
  div.max_modal #pokemon-screenBox .pokemon-screen {
    zoom: 1.8; }

div.max_modal .mobile-joystick {
  left: 3%;
  bottom: 15% !important; }
