div.max_modal .ty-wrap {
  width: 100%;
  height: calc(100% - 88px); }
  div.max_modal .ty-wrap .yt-video-item {
    width: calc(90% / 3) !important;
    height: 50%;
    padding: 1%; }

.ty-wrap {
  overflow: auto;
  background: #f9f9f9; }
  .ty-wrap .sidebar-box {
    width: 10%;
    height: calc(50vh - 38px);
    float: left;
    background: linear-gradient(to bottom, #fff, #f9f9f9); }
    .ty-wrap .sidebar-box .hamburger-menu {
      padding-left: 10px; }
      .ty-wrap .sidebar-box .hamburger-menu > button {
        border: none;
        background: #eee url("https://cdn1.iconfinder.com/data/icons/starter-mobile-app/100/mobile_button-16-512.png") no-repeat center center;
        background-size: 100%;
        padding: 15px;
        outline: none; }
  .ty-wrap .video-content {
    width: 90%;
    height: calc(50vh - 38px);
    float: left; }
    .ty-wrap .video-content .head-search-box {
      height: 30px; }
      .ty-wrap .video-content .head-search-box::after {
        display: block;
        clear: both;
        content: ''; }
      .ty-wrap .video-content .head-search-box > div {
        float: left; }
      .ty-wrap .video-content .head-search-box .v-head-search {
        position: relative; }
        .ty-wrap .video-content .head-search-box .v-head-search .yt-search-input {
          position: absolute;
          width: 190px;
          padding: 5px 5px;
          top: 0;
          left: 0;
          font-size: 1rem; }
        .ty-wrap .video-content .head-search-box .v-head-search .yt-search-btn {
          position: absolute;
          width: 80px;
          padding: 15px 0;
          background: url("https://kr.seaicons.com/wp-content/uploads/2015/06/Very-Basic-Search-icon.png") no-repeat center center;
          background-size: 20%;
          top: 0;
          left: 203px;
          outline: 0; }
    .ty-wrap .video-content .yt-video-item {
      width: 40%;
      height: 50%;
      padding: 5%;
      float: left; }
      .ty-wrap .video-content .yt-video-item > iframe {
        width: 100%;
        height: 100%; }
      .ty-wrap .video-content .yt-video-item .yt-video-title {
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden; }
