div.max_modal .painter-wrap {
  width: 100%;
  height: calc(100% - 88px); }

.painter-wrap {
  position: relative;
  background-color: #c8d2e1;
  height: calc(50vh - 38px);
  overflow: auto; }
  .painter-wrap .painter-options {
    height: 50px;
    background: linear-gradient(to bottom, #fff, #dde6f4);
    border-bottom: 1px solid #b7c4d6; }
    .painter-wrap .painter-options .painter-ation-box {
      position: absolute;
      top: 0;
      left: 0; }
    .painter-wrap .painter-options .painter-colos-box {
      width: 210px;
      position: absolute;
      right: 5px;
      top: 0; }
    .painter-wrap .painter-options ul::after {
      display: block;
      clear: both;
      content: ''; }
    .painter-wrap .painter-options ul li {
      width: 15px;
      height: 15px;
      margin: 2px;
      cursor: pointer;
      float: left; }
    .painter-wrap .painter-options ul.painter-ation .painter-save {
      background: url(data:image/png;base64,) no-repeat;
      background-size: 100%; }
    .painter-wrap .painter-options ul.painter-ation .painter-redo {
      background: url(/static/media/undo_icon.a6278998.png) no-repeat;
      background-size: 100%;
      transform: rotate(0deg);
      -webkit-transform: scaleX(-1);
      transform: scaleX(-1); }
    .painter-wrap .painter-options ul.painter-ation .painter-redo-gray {
      background: url(/static/media/undo_icon_gray.c39a8df8.png) no-repeat;
      background-size: 100%; }
    .painter-wrap .painter-options ul.painter-ation .painter-undo {
      background: url(/static/media/undo_icon.a6278998.png) no-repeat;
      background-size: 100%; }
    .painter-wrap .painter-options ul.painter-ation .painter-undo-gray {
      background: url(/static/media/undo_icon_gray.c39a8df8.png) no-repeat;
      background-size: 100%; }
    .painter-wrap .painter-options ul.painter-colos {
      right: 0; }
      .painter-wrap .painter-options ul.painter-colos > li {
        border: 1px solid #fffd;
        outline: 1px solid #aaa; }
    .painter-wrap .painter-options .painter-select-color-box {
      position: absolute;
      text-align: center;
      top: 1px;
      right: 240px;
      font-size: 10px; }
      .painter-wrap .painter-options .painter-select-color-box .select-color {
        padding: 10px;
        margin-bottom: 3px;
        background-color: #000;
        display: block;
        border: 1px solid #fffd;
        outline: 1px solid #aaa; }
    .painter-wrap .painter-options .painter-size-box {
      position: absolute;
      top: 20px;
      left: 0; }
  .painter-wrap #painter-boad {
    background-color: #fff;
    width: 100;
    height: 800;
    margin: 5px;
    box-shadow: 5px 5px 2px #b7c3d3; }
