.hidden {
  display: none; }

#login-wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000; }
  #login-wrap .login-bg {
    background: #000a00bd;
    width: 100vw;
    height: 100vh;
    -webkit-filter: blur(5px);
    -moz-filter: blur(5px);
    -o-filter: blur(5px);
    -ms-filter: blur(5px);
    filter: blur(5px); }
  #login-wrap .login-box {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translatex(-50%) translatey(-50%);
    -webkit-transform: translatex(-50%) translatey(-50%);
    color: #fff; }
  #login-wrap .login-profile-photo {
    padding: 100px;
    background: #aaa;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -khtml-border-radius: 50%;
    position: relative; }
    #login-wrap .login-profile-photo::before {
      position: absolute;
      top: 12%;
      left: 50%;
      transform: translateX(-50%);
      -webkit-transform: translatex(-50%);
      padding: 33px;
      border-radius: 50%;
      -webkit-border-radius: 50%;
      -moz-border-radius: 50%;
      -khtml-border-radius: 50%;
      content: '';
      display: block;
      border: 10px solid #fff; }
    #login-wrap .login-profile-photo::after {
      position: absolute;
      top: 52%;
      left: 50%;
      transform: translateX(-50%);
      -webkit-transform: translatex(-50%);
      padding: 20px 50px;
      border-top-left-radius: 7em;
      border-top-right-radius: 7em;
      content: '';
      display: block;
      border-top: 10px solid #fff;
      border-right: 10px solid #fff;
      border-left: 10px solid #fff;
      border-bottom: 0; }
  #login-wrap .login-profile-title {
    padding: 10% 0;
    text-align: center; }
  #login-wrap .loginner {
    background-color: #fff;
    display: flex; }
    #login-wrap .loginner input[type='password'] {
      width: 90%;
      padding: 5px;
      border: 0; }
    #login-wrap .loginner input[type='submit'], #login-wrap .loginner input[type='button'] {
      width: 10%;
      padding: 2px 3px;
      border: 1px solid #fff;
      background-color: #aaa;
      color: #fff;
      font-weight: bold;
      text-align: center; }
      #login-wrap .loginner input[type='submit']::hover, #login-wrap .loginner input[type='button']::hover {
        background-color: #999; }

.wrong-login {
  -webkit-animation-duration: 0.12s;
          animation-duration: 0.12s;
  -webkit-animation-name: wrongLogin;
          animation-name: wrongLogin;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate; }

@-webkit-keyframes wrongLogin {
  from {
    margin-left: 10px;
    margin-right: 0px; }
  to {
    margin-left: 0px;
    margin-right: 10px; } }

@keyframes wrongLogin {
  from {
    margin-left: 10px;
    margin-right: 0px; }
  to {
    margin-left: 0px;
    margin-right: 10px; } }
