#pokemon-screen-box {
  width: 100%;
  height: 91%;
  background: #000;
  outline: 0; }
  #pokemon-screen-box #pokemon-firstcity {
    width: 384px;
    height: 320px;
    background: #000;
    position: relative;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  #pokemon-screen-box .mobile-joystick {
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 5000;
    zoom: 0.8; }
    #pokemon-screen-box .mobile-joystick .mobile-controller {
      position: relative;
      width: 200px;
      height: 20vh;
      background-color: #fff;
      opacity: 0.3;
      border-radius: 20%; }
      #pokemon-screen-box .mobile-joystick .mobile-controller div {
        position: absolute; }
        #pokemon-screen-box .mobile-joystick .mobile-controller div button {
          padding: 20px;
          border: 5px solid #000;
          border-radius: 7em 0 0 7em;
          outline: 0; }
        #pokemon-screen-box .mobile-joystick .mobile-controller div:nth-child(1) {
          -webkit-transform: rotate(90deg);
                  transform: rotate(90deg);
          top: 0;
          left: 40%; }
          #pokemon-screen-box .mobile-joystick .mobile-controller div:nth-child(1) button {
            background: ddd; }
        #pokemon-screen-box .mobile-joystick .mobile-controller div:nth-child(2) {
          -webkit-transform: rotate(180deg);
                  transform: rotate(180deg);
          top: 35%;
          right: 0; }
        #pokemon-screen-box .mobile-joystick .mobile-controller div:nth-child(3) {
          -webkit-transform: rotate(270deg);
                  transform: rotate(270deg);
          left: 40%;
          bottom: 0; }
        #pokemon-screen-box .mobile-joystick .mobile-controller div:nth-child(4) {
          -webkit-transform: rotate(360deg);
                  transform: rotate(360deg);
          top: 35%; }
