div.max_modal .games-wrap {
  width: 100%;
  height: calc(100% - 88px); }
  div.max_modal .games-wrap .game-item {
    width: calc(100% / 5); }

.games-wrap {
  color: blanchedalmond;
  background-color: black;
  height: calc(50vh - 38px);
  overflow: auto; }
  .games-wrap > h3 {
    margin: 15px 0;
    text-align: center;
    font-size: 1.2rem;
    font-weight: bold; }
  .games-wrap .game-item {
    float: left;
    width: calc(90% / 2);
    padding: 0 2.5%; }
    .games-wrap .game-item.game-focus .game-box {
      border: 5px solid #fffa; }
    .games-wrap .game-item.game-select .game-box {
      border: 5px solid #fff !important; }
    .games-wrap .game-item .game-box {
      width: calc(100% - 5px);
      margin-bottom: 15%;
      border: 5px solid #000;
      text-align: center; }
      .games-wrap .game-item .game-box > .game-image {
        margin-bottom: 10px;
        height: 140px;
        background: black no-repeat center;
        background-size: 90%; }
